.breakout-conteiner{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    background: #230c33;
    display: flex;
    justify-content: center;
    align-items: center;
}
.breakout-conteiner #breakOut{
    background-color: #caa8f5;
    border-radius: 10px;
}