@tailwind base;
@tailwind components;
@tailwind utilities;


body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wrapper{
  width: 60%;
  background-color: blueviolet;
  height: 450px;
  border-radius: 8px;
  cursor: pointer;
  position: relative;
}
.image{
  width: 100%;
  height: 100%;
}
img{
  height: 100%;
  border-radius: 8px;
  width: 100%;
}
button{
  cursor: pointer;
  border: none;
  outline: none;
}
.start-btns button{
  font-size: 1.2em;
  padding: 1em 3em;
  background-color: #ffffff;
  border-radius: 2em;
}
.start-btns{
  position: absolute;
  top: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  opacity: 0;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.523) ;
  z-index: 1;
}
.wrapper:hover .start-btns{
    opacity: 1;
}

.modal-interval {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.672);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-interval .modal-numbers span {
    font-size: 3em;
    color: #fff;
    font-weight: 500;

}