.snake-conteiner{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    background: url('https://w0.peakpx.com/wallpaper/90/456/HD-wallpaper-buy-snake-pass-snake-game.jpg');
    background-size: cover;
}
.snake-conteiner .snake-body{
    width: 65vmin;
    height: 70vmin;
    background-color: #293447;
    border-radius: 5px;

}
.snake-conteiner .start button{
    padding: 12px 30px;
    color: white;
}
.snake-conteiner .stop button{
    padding: 12px 30px;
    color: white;
}
.snake-conteiner .board{
    width: 100%;
    height: 100%;
    position: relative;
    background-color: #212834;
    overflow: hidden;
    display: grid;
    grid-template-rows: repeat(21, 1fr);
    grid-template-columns: repeat(21, 1fr);
}
.snake-conteiner .board .head{
    border: 1px solid black;
}
